"use strict";

import './styles.css';
import CatalogueFilter from '../filter/filter';
import compiledTemplate from './template.hbs';
import Component from '../component'


/*
 * @fires 'phoneSelected'
 */
export default class PhoneCatalogue extends Component{
  constructor(options) {
    super(options.el);

    this._phones = [];
    this._render();
    this.on("click", this._onPhoneClick.bind(this), '[data-element="phone"]');
    document.addEventListener("filterCatalogue", this._onCatalogueFilter.bind(this));

    this._filter = new CatalogueFilter({
      el: document.querySelector('[data-component="filter"]')
    });
  }

  setPhones(phones) {
    this._phones = phones;
    this._render();
  }

  _onPhoneClick(event) {
    let phoneElement = event.target.closest('[data-element="phone"]');

    this.trigger("phoneSelected", phoneElement.dataset.phoneId);
  }

  _onCatalogueFilter(e) {
    let searchStr = e.detail.toLowerCase();
    let filteredPhones = this._phones.filter((element) => {
      return (element.name.toLowerCase().includes(searchStr) || element.snippet.toLowerCase().includes(searchStr));
    });
    this._render(filteredPhones);
  }

  _render(data = null) {
    if (data) {
      this._el.innerHTML = compiledTemplate({
        phones: data
      });
    } else {
      this._el.innerHTML = compiledTemplate({
        phones: this._phones
      });
    }
  }
}



