'use strict';

import Component from '../component';

export default class CatalogueFilter extends Component {
  constructor(options) {
    super(options.el)

    this.on('input', this._onInputFilter.bind(this));
  }

  _onInputFilter(e) {
    let filterValue = e.target.value;
    this.trigger('filterCatalogue', filterValue, true);
  }
}



